
import { defineComponent } from "vue";
export default defineComponent({
	name: "rowFrame",
	props: {
		title: {
			type: String
		},
		contentClass: {
			type: String
		},
		contentDefaultClass: {
			type: Boolean,
			default: false
		},
		flex: {
			type: Boolean,
			default: false
		},
		width: {
			type: String,
			default: "50%"
		}
	}
});
