import { request, getWs, setHeartBeat } from "@front/api/utils/websocket";
import { Event, event as sysEvent } from "@front/util_function/eventBus";

class WsEvent extends Event {
	registered = false;
	ws: WebSocket | null = null;
	id = "";
	constructor() {
		super();
		this.id = "";
	}

	register(id: string) {
		this.id = id;
		if (!this.ws) {
			this.ws = getWs();
			setHeartBeat(this.ws);
			this.ws.onclose = () => {
				this.ws = null;
				this.reRegister();
			};
		}
		if (!this.checkRegister()) {
			return request({
				method: "register",
				data: {
					type: 3,
					data: {
						clientID: id
					}
				},
				once: false,
				ws: this.ws
			}).then(() => {
				this.ws?.addEventListener("message", (e: any) => {
					const data = JSON.parse(e.data);
					if (data.type === 5) {
						try {
							if (data.error) {
								throw new Error(data.error);
							}

							const realData = JSON.parse(data.data.message);
							if (realData.sourceID !== this.id) {
								this.emit(realData.event, realData.data);
							}
						} catch (error) {
							console.error(error);
						}
					}
				});
				this.ws?.addEventListener("close", (e: any) => {
					this.registered = false;
				});
				this.registered = true;
				this.id = id;
				this.emit("registered");
			});
		}
		return Promise.resolve();
	}

	reRegister() {
		if (this.id) {
			return this.register(this.id);
		} else {
			console.log("not registered yet!");
			return false;
		}
	}

	checkRegister() {
		if (!this.registered) console.log("unregistered yet!");
		return this.registered;
	}

	wsEmit(eventName: any, data: any = {}, targetID = "") {
		const promise = () => {
			return request({
				method: "message-broadcast",
				data: {
					type: 4,
					data: {
						clientID: targetID,
						message: JSON.stringify({
							sourceID: this.id,
							event: eventName,
							data
						})
					}
				}
			});
		};
		if (this.checkRegister()) {
			return promise();
		}
		//  else {
		// 	this.once("registered", promise);
		// 	return true;
		// }
	}
}

export const wsevent = new WsEvent();
