
import { defineComponent } from "vue";
import { load } from "@front/util_function/file";
import { uploadBase64Image, path } from "@front/util_function/system";
import { BlobtoB64 } from "@front/util_function/imgTool";
import { ElMessage } from "element-plus";
import VueCropper from "vue-cropperjs";
import "cropperjs/dist/cropper.css";
export default defineComponent({
	name: "imgInputClip",
	components: { VueCropper },
	props: {
		modelValue: {
			type: String,
			default: ""
		},
		fit: {
			type: String,
			default: "cover"
		}
	},
	data() {
		const editImg: any = "";
		return {
			clipDialog: false,
			editImg
		};
	},
	computed: {
		src(): any {
			return this.modelValue;
		}
	},
	methods: {
		async getFile() {
			const file: any = await load(".jpg,.png,.gif");
			if (file) {
				this.edit(file);
			}
		},
		edit(file: any) {
			BlobtoB64(file)
				.then(res => {
					this.editImg = res;
					this.clipDialog = true;
				})
				.catch(() => {
					ElMessage({
						message: "图片读取错误",
						duration: 1500,
						type: "error",
						offset: 60
					});
				});
		},
		crop() {
			// @ts-ignore
			const cropImg = this.$refs.cropper.getCroppedCanvas().toDataURL();
			uploadBase64Image(cropImg)
				.then(url => {
					this.$emit(
						"update:modelValue",
						`/configFiles/images/${path.basename(url)}`
					);
					this.editImg = "";
					this.clipDialog = false;
				})
				.catch(() => {
					ElMessage({
						message: "图片保存失败",
						duration: 1500,
						type: "error",
						offset: 60
					});
				});
		}
	}
});
