
import { defineComponent } from "vue";
export default defineComponent({
	name: "imgInputBase",
	props: {
		src: {
			type: String,
			default: ""
		},
		editable: {
			type: Boolean,
			default: true
		}
	},
	data() {
		const blob: any = null;
		return {
			tips: false,
			msg: "",
			blob
		};
	},
	computed: {
		img: {
			get(): any {
				return this.src;
			},
			set(e: any): any {
				this.$emit("update:src", e);
			}
		}
	},
	methods: {
		showTips(e: any) {
			e.preventDefault();
			// console.log(e);
			if (!this.editable) {
				return;
			}
			if (e.type === "mouseleave" || e.type === "dragleave") {
				this.tips = false;
				return;
			}
			if (e.buttons === 1) {
				this.msg = "将照片拖到此处释放";
			} else {
				this.msg = "点击或将图片拖到此处";
			}
			this.tips = true;
		},
		getFile() {
			this.$el.querySelector("input").click();
		},
		loadFile(e: any) {
			let files = new File([""], "");
			if (e.dataTransfer) {
				for (let i = 0; i < e.dataTransfer.items.length; i++) {
					// If dropped items aren't files, reject them
					if (e.dataTransfer.items[i].kind === "file") {
						files = e.dataTransfer.items[i].getAsFile();
					}
				}
			} else {
				files = e.srcElement.files[0];
			}
			this.blob = files;
			const reader = new FileReader();
			reader.readAsDataURL(files);
			reader.onloadend = () => {
				this.img = reader.result;
			};
		}
	}
});
