
import { defineComponent } from "vue";
export default defineComponent({
	name: "titleScrolling",
	props: {
		label: {
			type: String,
			default: ""
		}
	},
	data() {
		const observer: any = null;
		const observerTimer: any = null;
		return {
			titleMove: 0,
			observer,
			observerTimer,
			mounted: false
		};
	},
	computed: {
		style(): any {
			return {
				"--titleMove": `${this.titleMove}px`,
				"--titleMoveTime": `${-this.titleMove / 4}s`
			};
		}
	},
	watch: {
		label: {
			handler(n, o) {
				if (this.mounted && n) {
					this.addObserver();
				}
			},
			immediate: true
		}
	},
	mounted() {
		this.addObserver();
	},
	beforeUnmount() {
		if (this.observer) {
			this.observer.disconnect();
		}
	},
	methods: {
		addObserver() {
			try {
				const parent: any = this.$el;
				if (!parent) {
					throw new Error();
				}
				const child = parent.firstChild;
				const getMove = () => {
					const move = child.offsetWidth - parent.offsetWidth;
					if (move > 0) {
						this.titleMove = -move - 10;
					} else {
						this.titleMove = 0;
					}
				};
				if (this.observer) {
					this.observer.disconnect();
				}
				// @ts-ignore
				this.observer = new ResizeObserver(getMove);

				this.observer.observe(parent);
				this.observer.observe(child);
				getMove();
			} catch (error) {}
		}
	}
});
